import common from '../../common/common.js';
import helloComp from '../../componente/helloComp.vue';
import trailerStars from '../../componente/trailerStars.vue';
export default {
	data() {
		return {
			carouselList: [],
			hotSuperheroList: [],
			hotTrailerList: [],
			guessULikeList: [],
			animationData: {},
			animationDataArr: [{}, {}, {}, {}, {}]
		};
	},
	
	
	onHide() {
		
		if(this.videoContext){
			this.videoContext.pause();
		}
		
	},
	onShow() {
		
		if(this.videoContext){
			this.videoContext.play();
		}
		
	},
	
	
	onUnload() {
		this.animationData = {};
		this.animationDataArr = [{}, {}, {}, {}, {}]
	},
	onPullDownRefresh(){
		this.refresh();
	},
	onLoad() {
		var me = this;

		this.animation = uni.createAnimation();

		//请求轮播图
		var serverUrl = common.serverUrl;

		uni.request({
			url: serverUrl + '/index/carousel/list?qq=123', //仅为示例，并非真实接口地址。
			method: 'POST',
			success: res => {
				// console.log(res.data);
				//获取真实数据前,判断状态是否为200
				if (res.data.status == 200) {
					var carouselList = res.data.data;
					me.carouselList = carouselList;
				}
			}

			/* var me =this;
			success: function () {
				
			
				console.log(res.data);
				//获取真实数据前,判断状态是否为200
				if( res.data.status == 200)
				var carouselList = res.data.data;
				me.carouselList =carouselList
				
			} */
		});

		//查询热门超英
		uni.request({
			url: serverUrl + '/index/movie/hot?type=superhero&qq=123', //仅为示例，并非真实接口地址。
			method: 'POST',
			success: res => {
				// console.log(res.data);
				//获取真实数据前,判断状态是否为200
				if (res.data.status == 200) {
					var hotSuperheroList = res.data.data;
					me.hotSuperheroList = hotSuperheroList;
				}
			}
		});

		//查询热门预览
		uni.request({
			url: serverUrl + '/index/movie/hot?type=trailer&qq=123', //仅为示例，并非真实接口地址。
			method: 'POST',
			success: res => {
				// console.log(res.data);
				//获取真实数据前,判断状态是否为200
				if (res.data.status == 200) {
					var hotTrailerList = res.data.data;
					me.hotTrailerList = hotTrailerList;
				}
			}
		});

		//猜你喜欢
		uni.request({
			url: serverUrl + '/index/guessULike?qq=123', //仅为示例，并非真实接口地址。
			method: 'POST',
			success: res => {
				// console.log(res.data);
				//获取真实数据前,判断状态是否为200
				if (res.data.status == 200) {
					var guessULikeList = res.data.data;
					me.guessULikeList = guessULikeList;
				}
			}
		});
		
		me.refresh()
	},
	methods: {
		meIsPlaying(e){
			var me =this;
			var trailerId = "";
			//debugger
			if (e){
				trailerId =e.currentTarget.dataset.playingindex;
				me.videoContext = uni.createVideoContext(trailerId);
				
				
			}
			var hotTrailerList =me.hotTrailerList;
			for(var i=0;i<hotTrailerList.length;i++){
				var tempId = hotTrailerList[i].id;
				if (tempId != trailerId){
					uni.createVideoContext(tempId).pause();
				}
			}
		},
		
		
		
		
		refresh(){
			var me = this;
		
			var serverUrl = common.serverUrl;
			uni.showLoading({
				mask:true
			});
			uni.showNavigationBarLoading()
			//猜你喜欢
			uni.request({
				url: serverUrl + '/index/guessULike?qq=123', //仅为示例，并非真实接口地址。
				method: 'POST',
				success: res => {
					// console.log(res.data);
					//获取真实数据前,判断状态是否为200
					if (res.data.status == 200) {
						var guessULikeList = res.data.data;
						me.guessULikeList = guessULikeList;
					}
				},
				complete: () => {
					uni.hideLoading();
					uni.stopPullDownRefresh();
					uni.hideNavigationBarLoading()
					
				}
			});
		},
		
		praiseMe(e) {

			var gIndex = e.currentTarget.dataset.gindex;
			//console.log(gIndex);
			//return;
			this.animation
				.translateY(-60)
				.opacity(1)
				.step({
					duration: 400
				});
			//this.animationData = this.animation.export();
			this.animationData = this.animation;
			this.animationDataArr[gIndex] = this.animationData.export();

			setTimeout(
				function() {
					this.animation
						.translateY(0)
						.opacity(0)
						.step({
							duration: 0
						});
					//this.animationData = this.animation.export();
					this.animationData = this.animation;
					this.animationDataArr[gIndex] = this.animationData.export();
				}.bind(this),
				500
			);

		}
	},
	components: {
		helloComp,
		trailerStars
	}
};
